import React from "react";
import {
  Grid,
  AppBar,
  Typography,
  Button,
  IconButton,
  Toolbar,
  Avatar,
  Paper,
  Step,
  StepLabel,
  Stepper,
  StepContent,
  StepConnector
} from "@material-ui/core";
import Education from "./education";
import useStyles from "./styles";
import EditIcon from "@material-ui/icons/Edit";
import Call from "@material-ui/icons/Call";
import MyLocation from "@material-ui/icons/MyLocation";
import Email from "@material-ui/icons/Email";
import Experiance from "./experience";
import Certification from "./certifications";
import Events from "./Events";
import Profileoverview from "./profileoverview";
import Heighlights from "./highlights";

export default props => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <AppBar color="primary" position="static">
        <Toolbar>
          <img
            src="data:image/png;base64,"
            alt="name"
            height="30px"
            width="90px"
          />
          <Typography className={classes.title}></Typography>
          <Button color="inherit">Share</Button>
          <Button color="inherit">Download Resume</Button>
          <Button color="inherit">Logout</Button>
        </Toolbar>
      </AppBar>
      <Grid container className={classes.containerClass}>
        <Grid item xs={4}>
          <Paper className={classes.paper}>
            <Grid style={{ display: "flex" }}>
              <Avatar
                alt="Remy Sharp"
                src="https://wallpapercave.com/wp/OF3Zstl.jpg"
                className={classes.bigAvatar}
              />
              <IconButton
                edge="start"
                color="inherit"
                aria-label="menu"
                className={classes.EditIconLeft}
              >
                <EditIcon />
              </IconButton>
            </Grid>
            <hr />
            <Grid container>
              <Grid className={[classes.contentClass,classes.TextFieldTopMargin].join(" ")}>
                <Email />
                <Typography variant="span" className={classes.marginLeft}>
                  sh@gmail.com
                </Typography>
              </Grid>
            </Grid>
            <Grid container>
              <Grid className={[classes.contentClass,classes.TextFieldTopMargin].join(" ")}>
                <Call />
                <Typography variant="span" className={classes.marginLeft}>
                  545454546546
                </Typography>
              </Grid>
            </Grid>
            <Grid container>
              <Grid className={[classes.contentClass,classes.TextFieldTopMargin].join(" ")}>
                <MyLocation />
                <Typography variant="span" className={classes.marginLeft}>
                  Bengalore
                </Typography>
              </Grid>
            </Grid>
          </Paper>
          <Heighlights />
        </Grid>
        <Grid item xs={8}>
          <Profileoverview />
          <Experiance />

          <Education />
          <Certification />
          <Events />
        </Grid>
      </Grid>
    </div>
  );
};
